.settings{
    display: flex;
}
.settingsWrapper{
    flex: 9;
}

.settingsTitle{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.settingsUpdateTitle{
    font-size: 30px;
    margin-bottom: 20px;
    color: brown;
}

.settingsDeleteTitle{
    color: red;
    font-size: 12px;
    cursor: pointer;
}

.settingsForm{
    display: flex;
    flex-direction: column;
}

.settingsProfilePicture{
    display: flex;
    align-items: center;
    margin: 10px 0;
    
}

.settingsProfilePicture > img{
    width: 70px;
    height: 70px;
    border-radius: 20px;
    object-fit: cover;
}

.settingsPPIcon{
    height: 25px;
    width: 25px;
    background-color: gray;
    color: white;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-left: 10px;
    cursor: pointer;
}

.settingsForm > label{
    font-size: 20px;
    margin-top: 20px;
}

.settingsForm > input{
    color: gray;
    margin: 10px 0;
    height: 30px;
    border: none;
    border-bottom: 1px solid gray;
}

.settingsUpdate{
    widows: 170px;
    align-self: center;
    border: none;
    border-radius: 10px;
    color: white;
    background-color: gray;
    padding: 10px;
    
    margin-top: 20px;
    cursor: pointer;
}